  /* Animation*/

  @keyframes spin1 {
    0%  {transform: rotate(0deg);}
    50%{transform: rotate(-35deg);}

}
@keyframes spin2 {
    0%  {transform: rotate(0deg);}
  50%{transform: rotate(35deg);}
}

@-moz-keyframes spin1 {
    0%  {transform: rotate(0deg);}
    50%{transform: rotate(-35deg);}

}
@-moz-keyframes spin2 {
    0%  {transform: rotate(0deg);}
  50%{transform: rotate(35deg);}
}

@-webkit-keyframes spin1 {
    0%  {transform: rotate(0deg);}
    50%{transform: rotate(-35deg);}

}
@-webkit-keyframes spin2 {
    0%  {transform: rotate(0deg);}
  50%{transform: rotate(35deg);}
}


.trajectory{
    position: relative;
    overflow: hidden;
    background-image: linear-gradient(to bottom left, var(--lila), var(--azul2), var(--azul));
}
.trajectory .container{
    position: relative;
}

.scroll-container{
    position: relative;
    width: 100%;
    height: 42rem;
    overflow-x: scroll;
    padding: 2rem 3rem;
    border-radius: 1rem;
    background-color: white;
    box-shadow: inset 0 0 5px var(--navy);
    border: 5px solid var(--navy);
    border-bottom: 2rem solid var(--navy);
}
.timeline{
    flex-wrap: nowrap;
    justify-content: center;
    flex-direction: row;
    position: absolute;
}

.timeline-event{
    width: 4rem;
    height: 4rem;
    border-radius: 50%;
    position: relative;
    margin-right: 5.5rem;
    margin-top: 14rem;
    font-size: 1rem;
}


.timeline-event > span{
    position: absolute;
    width: 6rem;
    height: .5rem;
    right: -6rem;
    top: calc(50% - .25rem);
}

.timeline-icon{
    width: 6rem;
    height: 6rem;
    position: absolute;
    top: -12rem;
    left: -1.5rem;
    border-radius: 50%;
    color: white;
    text-align: center;
    font-size: 3rem;
    padding-top: calc( 1.5rem);
}

.indicador{
    position: absolute;
    top: -5.5rem;
    left: calc(50% - 1rem);
    border-left: 1rem solid transparent;
    border-right: 1rem solid transparent;
}


.timeline-icon > span{
    width: 3rem;
    height: 3rem;
    position: absolute;
    bottom: -1.5rem;
}
.timeline-desc{
    position: absolute;
    top: 6rem;
    left: -3rem;
    width: 10rem;
    padding: 1rem;
    font-size: 1.2rem;
    color: #5F6A6A;
}

.pacman{
    position: absolute;
    left: -5rem;
    top: 21rem;
    z-index: 2;
}

.pacman-top{
    background-color:yellow;
    height: 5rem;
    width: 10rem;
    border-radius: 100px 100px 0 0;

}

.pacman-bottom{
    background-color:yellow;
    height: 5rem;
    width: 10rem;
    border-radius:0 0 100px 100px;

}

  .code-il{
      position: absolute;
      bottom: -6rem;
      right: -6rem;
      width: 12rem;
  }


.circle{
    border-radius: 50%;
    margin: 0 auto;
}

.circle-1{
    width: 30rem;
    height: 30rem;
    padding-top: 5rem;
    position: absolute;
    right: -10rem;
    top: -15rem;
    background-color: rgba(245, 148, 0, .5);
}
.circle-2{
    width: 20rem;
    height: 20rem;
    padding-top: 5rem;
    background-color: rgba(245, 148, 0, .7);
}
.circle-3{
    width: 10rem;
    height: 10rem;
    background-color: rgba(245, 148, 0, .9);
}
.keyboard-container{
    width: 100%;
}
.keyboard{
    display: none;
}


@media only screen and (min-width: 64rem){
  .scroll-container{
      height: 50rem;
    }
   .timeline-event{
     width: 6rem;
     height: 6rem;
     font-size: 1.6rem;
     margin-top: 16rem;
    }
    .timeline-icon{
        width: 8rem;
        height: 8rem;
        top: -14rem;
        left: -1.5rem;
        font-size: 4rem;
        padding-top: calc(100% - 3rem);
    }
    .indicador{
        top: -5.5rem;
        left: calc(50% - 1rem);
    }
    .timeline-desc{
        top: 10rem;
        left: -3rem;
        font-size: 1.2rem;
    }
    .keyboard{
        width: 50%;
        margin: 0 auto;
    }
    .pacman{
        position: absolute;
        left: -5rem;
        top: 25rem;
        z-index: 2;
    }
}
